import { Widget } from './components/ChatWidget/index.vue'

// Create the mount function
function mount(el, config = {}) {
  if (!window.Vue) {
    throw new Error('Vue is not loaded');
  }
  const { createApp, h } = window.Vue;

  // Create a wrapper div if string selector is provided
  const container = typeof el === 'string' ? document.querySelector(el) : el;
  if (!container) {
    throw new Error('Mount element not found');
  }

  // Create app instance using global Vue
  const app = createApp({
    name: 'SophosicChatApp',
    render() {
      return h(Widget, { config });
    }
  });

  // Mount with proper DOM handling
  try {
    app.mount(container);
    return app;
  } catch (err) {
    console.error('Failed to mount app:', err);
    throw err;
  }
}

// Create the library object
const SophosicChat = {
  Widget,
  mount
};

// Add to window object if in browser
if (typeof window !== 'undefined') {
  window.SophosicChat = SophosicChat;
}

// Export named exports and default
export { Widget, mount }
export default SophosicChat 